.testimonial__container{
    display: flex;
    column-gap: 3rem;
    box-shadow: 0 0 1.5rem 10px #e7e7e7;
    padding: var(--mb-2-5);
    border-radius: 1rem;
}

.testimonial__img{
    width: 128px;
    height: 128px;
    border-radius: 100%;
    align-self: center;
    object-fit: cover;
}

.star{
    font-size: var(--normal-font-size);
}

.testimonial__text{
    gap: .5rem;
}

.testimonial__name{
    font-size: var(--normal-font-size);
    font-weight: var(--font-semi-bold);
}