#footer {
    padding: 6rem;
    background-color: rgb(249 250 251);
    display: flex;
    flex-direction: column;
}

.footer-main {
    display: flex;
}

.footer-main .child {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.footer-main .child.large-text span{
    font-size: 1.5rem;
    line-height: 2rem;
    margin-bottom: 1rem;
    font-weight: 700;
    cursor: pointer;
}

.footer-main .child.small-text span {
    font-size: .875rem;
    line-height: 1.25rem;
}

.footer-main .child.small-text span:first-child {
    margin-bottom: 0.75rem;
    font-weight: 700; 
}

.footer-main .child.small-text span:not(:first-child) {
    margin-bottom: 0.5rem;
    font-weight: 500; 
    cursor: pointer;
}

.tag-line {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: .875rem;
    line-height: 1.25rem;
    margin-top: 0.5rem;
}

.tag-line img {
    height: 1rem;
    margin: 0 0.5rem;
}

.language-selection {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: end;
    width: max-content;
}

.language-selection .title {
    margin-bottom: 0.25rem;
    font-size: .875rem;
    line-height: 1.25rem;
}

.language-selection select {
    width: 100%;
    color: rgba(0, 0, 0, 0.5);
}



#footer .social {
    display: flex;
    justify-content: center;
}

#footer .social svg {
    font-size: 1.5rem;
    line-height: 2rem;
    cursor: pointer;  
    color: rgb(75 85 99/1);
    margin: 0 1rem;
}
