.guarantee{
    margin: 0 8rem;
}
.guarantee__container{
    display: grid;
    grid-template-columns: 10rem 1fr;
    border: 1px solid var(--border-color);
    border-radius: 1rem;
    padding:var(--mb-2-5);
    margin: 0 var(--mb-2);
    box-shadow: 0px 10px 50px 0px rgb(0 7 40 / 23%);
}

.guarantee__img{
    padding: var(--mb-1);
}

.guarantee__note{
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 1rem;
}

.guarantee__note>h2{
    color: rgba(0, 0, 0, 0.5);
    font-size: var(--normal-font-size);
}

.signature{
    height: 4rem;
    margin: var(--mb-2-5) 0 -1rem -2rem;
}