.premium{
    padding-top: 0;
    padding-left: 8rem;
    padding-right: 8rem;
}

.premium__content{
    grid-template-columns: repeat(2, 1fr);
}

.premium__image{
    padding-bottom: 20rem;
}

.premium__data{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

/* ====================== BREAKPOINTS ====================== */
/* Medium Device */
@media screen and (max-width: 768px) {
    .premium__content{
        grid-template-columns: 1fr;
    }
    
}

