.features{
    margin-left: 8rem;
    margin-right: 8rem;
}
.data__grid {
    display: grid;
	grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
}

.data__item{
    margin-right: 1rem;
    margin-bottom: 3rem;
}

/* ====================== BREAKPOINTS ====================== */
/* Medium Device */
@media screen and (max-width: 768px) {
    .data__grid{
        grid-template-columns: 1fr;
        margin: 0 5rem;
    }
}

